@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.catalog-page {
    background-color: #fff;

    * {
        font-family: 'Myriad Pro' !important;
        font-weight: 400;
        font-style: normal;
        color: $text-main-color;
        text-decoration: none;

        min-height: 0;
        min-width: 0;
    }

    #filter-range {
        height: 3px;
    }

    .ui-slider-horizontal .ui-slider-handle {
        top: -.5em;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        outline: none;
        background: #fff;

        display: flex;
        justify-content: center;
        align-items: center;


        &::before {
            content: '';
            width: 14px;
            height: 6px;
            background-image: url('../img/ac-arrow.svg');
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            display: block;
            margin: 0 auto;
            transform: rotate(90deg);
        }

        &:last-child::before {
            transform: rotate(-90deg);
        }
    }

    .ui-widget-header {
        background: $main-color;
        border: $main-color;
    }

    #slider-range {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 17px;

        input {
            max-width: 95px;
            width: 100%;
            border: 1px solid #EBEBEB;
            outline: none;
            padding: 7px 9px;
            box-sizing: border-box;
        }
    }

    .btn {
        @include page-btn();
    }

    .ac-trigger {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;

        &::after {
            content: '';
            display: block;
            background-image: url('../img/ac-arrow.svg');
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            min-height: 9px;
            max-width: 10px;
            width: 100%;
            transition: transform .3s;
            transform: rotate(-90deg);
        }

        &._active::after {
            transform: rotate(0deg);
        }
    }

    .info-trigger {
        cursor: pointer;
        width: 10px;
        height: 10px;
        background: #D9D9D9;
        border-radius: 50%;

        display: flex;
        justify-content: center;
        align-items: center;

        position: absolute;
        top: 7px;
        right: -11px;

        &::before {
            content: 'i';
            display: block;
            color: #fff;
            font-size: 8px;
            position: relative;
            right: 0.5px;
        }

        &:hover .info-content {
            visibility: visible;
        }
    }

    .info-content {
        background-color: #fff;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        padding: 12px 13px;
        max-width: 246px;
        width: 246px;
        position: absolute;
        bottom: 5px;
        left: 5px;
        z-index: 10;

        visibility: hidden;
        pointer-events: none;

        .info-list {}

        .info-list__item {
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 140%;


            color: #000;

            &::before {
                content: '•';
                color: #B34215;
                font-weight: 700;
                font-size: 20px;
                line-height: 140%;
                display: inline;
            }
        }

        .info__img {
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover
        }

        .info__header {
            font-style: normal;
            font-weight: 600 !important;
            font-size: 14px !important;
            line-height: 140% !important;

            color: #000000;
            display: block;
            margin-bottom: 4px;
            margin-top: 154px;
        }

        .info__desc {
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;

            color: #000000;
        }
    }

    .section-header {
        @include section-header();
    }

    .section-text {
        @include section-text();
    }

    .container {
        max-width: 1140px;
        width: 100%;
        margin: 0 auto;
        padding: 0 15px;
    }

    .headline {
        padding: 96px 0 46px;

        .container {
            position: relative;
        }

        .headline__wrapper {
            max-width: 745px;
        }

        .headline__header {
            text-align: left;
            margin-bottom: 25px;
        }

        .headline__desc {
            max-width: 628px;
        }

        .headline__img {
            position: absolute;
            right: -85px;
            top: 2px;
            max-width: 60%;
        }
    }

    .catalog {
        padding: 28px 0 60px;
        background: #F9F9F9;

        .container {}

        .catalog__top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 16px;
        }

        .breadcrumbs {
            display: flex;
            justify-content: start;
            align-items: center;
            flex-wrap: wrap;
        }

        .breadcrumbs__item {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;

            color: #000000;
            text-decoration: none;

            display: flex;
            justify-content: start;
            align-items: center;

            &:not(:last-child)::after {
                content: '';
                display: block;
                margin: 0 10px;
                height: 1px;
                min-width: 7px;
                background: #37383D;
            }
        }

        .catalog__sort__trigger {
            display: none;
            background: #FFFFFF;
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
            border-radius: 5px;
            padding: 6px 15px 6px 7px;

            justify-content: start;
            align-items: center;

            img {
                margin-right: 18px;
            }
        }

        .catalog__sort__wrapper {
            display: flex;
            justify-content: center;

            span {
                font-style: normal;
                font-weight: 600;
                font-size: 12px;
                line-height: 14px;
                display: flex;
                align-items: center;

                color: #000000;
            }
        }

        .catalog__sort__select-wrap {
            margin-left: 10px;
            position: relative;
        }

        .catalog__sort__select__current {
            font-style: normal;
            font-weight: 300 !important;
            font-size: 12px;
            line-height: 14px;
            display: flex;
            align-items: center;
            cursor: pointer;

            color: #000000;

            &::after {
                content: '';
                width: 13px;
                height: 13px;
                display: block;
                background-image: url('../img/filter-arrow.png');
                margin-left: 10px;
                transform: rotate(0deg) !important;
            }
        }

        .catalog__sort__select__list {
            position: absolute;
            top: 17px;
            left: -24px;
            width: 175px;
            box-sizing: border-box;
            background-color: #fff;
            padding: 5px 15px;
            z-index: 10;
        }

        .catalog__sort__select__list__item {
            cursor: pointer;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 200%;
            text-align: left;
            color: #000000;
        }

        .catalog__body {
            display: grid;
            grid-template-columns: 280px auto;
            column-gap: 40px;
            align-items: flex-start;
        }

        .catalog__side-bar {
            grid-area: "2";
        }

        .catalog__filters {
            background: #FFFFFF;
            /* Shadow */
            box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
            padding: 20px 16px 26px;
            box-sizing: border-box;
            margin-bottom: 22px;
        }

        .catalog__filters__mobile {
            display: none;
            padding: 21px 0 24px;
        }

        .catalog__filters__close {
            border: 0.5px solid #37383D;
            border-radius: 5px;
            background: transparent;
            padding: 9px;
            cursor: pointer;

            display: flex;
            justify-content: start;
            align-items: center;

            &::before {
                content: '';
                width: 6px;
                height: 11px;
                background-image: url('../img/Union.svg');
                background-position: center;
                background-repeat: no-repeat;
                display: block;
                margin-right: 12px;
            }
        }

        .catalog__filters__wrap {
            margin-bottom: 33px;
        }

        .catalog__filters__search-form {}

        .catalog__filters__search-label {
            position: relative;

            &::before {
                content: '';
                width: 15px;
                height: 15px;
                background-image: url('../img/search.svg');
                display: block;
                position: absolute;
                top: 50%;
                left: 13px;
                transform: translateY(-50%);
            }

            &:focus-within::before {
                visibility: hidden;
            }
        }

        .catalog__filters__search-field {
            width: 100%;
            border: 1px solid #EBEBEB;
            outline: none;
            padding: 13px 13px 12px 35px;
            box-sizing: border-box;

            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: $text-main-color;

            &::placeholder {
                text-align: center;
                color: #D1D1D1;
            }

        }

        .catalog__filter__ac-wrap {
            margin-bottom: 30px;
        }

        .catalog__filter__search-form__controlls {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .catalog__filter__search-form__submit {
            max-width: 144px;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            text-transform: unset;
        }

        .catalog__filter__search-form__reset {
            cursor: pointer;
            border: none;
            outline: none;
            background-color: transparent;

            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;

            color: #37383D;

            display: flex;
            justify-content: start;
            align-items: center;

            &::before {
                content: '';
                width: 11px;
                height: 11px;
                display: block;
                background-image: url('../img/cross.svg');
                margin-right: 10px;
            }
        }

        .catalog__filter__header {
            display: flex;
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 18px;

            color: #000000;
            margin-bottom: 15px;
        }

        .catalog__filter__range-wrap {
            margin-bottom: 17px;
        }

        .catalog__filter__list {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 15px 5px;
            align-items: flex-start;

            &._one-column {
                grid-template-columns: 1fr;
            }

            &:not(:last-child) {
                margin-bottom: 12px;
                border-bottom: 1px solid #F3F3F3;
                padding-bottom: 12px;
            }
        }

        .catalog__filter__list__item {}

        .catalog__filter__list__item__label {
            display: flex;
            justify-content: start;
            align-items: center;
            position: relative;

            span {
                font-style: normal;
                font-weight: 300;
                font-size: 12px;
                line-height: 14px;

                color: #000000;
            }

            .info-trigger {
                position: relative;
                top: 0;
                left: 0;
                margin-left: 6px;

                &::before {
                    content: '?';
                }
            }
        }

        .catalog__filter__list__item__check {
            visibility: hidden;
            pointer-events: none;
            position: absolute;
            top: 0;
            left: 0;

            &:checked+.catalog__filter__list__item__fake-check::before {
                visibility: visible;
            }
        }

        .catalog__filter__list__item__fake-check {
            min-width: 17px;
            height: 17px;
            box-sizing: border-box;

            border: 1px solid #EBEBEB;
            margin-right: 6px;

            &::before {
                content: '';
                width: 80%;
                height: 100%;
                display: block;
                margin: 0 auto;
                background-image: url(../img/check.png);
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                visibility: hidden;
            }
        }

        .catalog__banner {
            box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        }

        .catalog__banner__img-box {
            width: 100%;

            img {
                width: inherit;
                object-fit: cover;
            }
        }

        .catalog__banner__body {
            box-sizing: border-box;
            padding: 14px 16px;
            background: #FFFFFF;
        }

        .catalog__banner__list {
            margin-bottom: 16px;
        }

        .catalog__banner__list__item {
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 21px;

            /* or 28px */

            color: #000;

            &::before {
                content: '•';
                color: $main-color;
                font-weight: 700;
                font-size: 20px;
                line-height: 140%;
                display: inline;
            }
        }

        .catalog__banner__phone {
            font-style: normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 140%;
            /* identical to box height, or 25px */
            color: #000000;
            margin-bottom: 18px;
            display: block;
        }

        .catalog__banner__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .catalog__banner__btn {
            // max-width: 113px;
            display: flex;
            justify-content: start;
            align-items: center;
        }


        .catalog__list {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(310px, 398px));
            gap: 22px 24px;
            grid-area: "1";
        }

        .catalog__list__item {
            background: #FFFFFF;
            box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
        }

        .catalog__list__item__img-box {
            width: 100%;
            position: relative;

            img {
                width: inherit;
                object-fit: cover;
            }
        }

        .hot {
            max-width: 131px;
            width: 100%;
            position: absolute;
            top: 0;
            right: 0;
            background: #C83636;
            border-radius: 0px 0px 0px 30px;

            display: flex;
            justify-content: start;
            align-items: center;
            padding: 10px 12px 9px 20px;
            box-sizing: border-box;

            &.popular {
                background: #44C554;

            }

            img {
                width: 20px;
                height: 20px;
            }

            span {
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 100%;
                color: #FFFFFF;
                margin: 0 auto;
                text-align: center;
                max-width: 69px;
            }
        }

        .catalog__list__item__desc {
            padding: 21px 20px 15px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            min-height: 140px;
        }

        .catalog__list__item__wrapper {
            display: grid;
            grid-template-columns: auto auto;
            justify-content: space-between;
        }

        .catalog__list__item__name {
            font-style: normal;
            font-weight: 600;
            font-size: 23px;
            line-height: 140%;
            /* or 32px */
            color: #000000;
        }

        .catalog__list__item__square {
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            /* identical to box height, or 28px */
            text-align: right;
            color: #000000;
        }

        .catalog__list__item__price {}

        .actual-price {
            position: relative;
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 100%;
            /* identical to box height, or 28px */
            color: #B34215;
        }

        .old__price {
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 100%;
            display: block;
            /* or 22px */
            text-decoration-line: line-through;
            color: #ADADAD;
        }

        .catalog__list__item__type {
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 17px;
            /* identical to box height */
            text-align: right;
            color: #000000;
        }

        .catalog__list__item__list {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
        }

        .catalog__list__item__list__item {
            display: flex;
            justify-content: start;
            align-items: center;
            flex-direction: column;
            margin-top: auto;

            img {
                height: 26px;
                width: 26px;
            }

            span {
                font-style: normal;
                font-weight: 300;
                font-size: 15px;
                line-height: 140%;
                /* identical to box height, or 21px */
                text-align: center;
                color: #000000
            }
        }

        .show-more {
            max-width: 100%;
            margin-top: 30px;
        }

        .catalog__pager {
            display: none;
            justify-content: space-between;
            align-items: center;
            max-width: 345px;
            width: 100%;
            margin: 30px auto;

            .catalog__pager__link {
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 14px;
                display: flex;
                align-items: center;
                text-align: center;
                text-transform: uppercase;

                color: #000000;

                &._active {
                    color: $main-color;
                }
            }

        }
    }

    .building {
        padding: 90px 0 124px;

        .container {}

        .building__heading {
            max-width: 874px;
            margin: 0 auto 61px;
        }

        .building__wrapper {
            display: grid;
            grid-template-columns: minmax(310px, 580px) minmax(310px, 463px);
            justify-content: space-between;
        }

        .building__desc {
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            line-height: 140%;
            /* or 25px */
            color: #37383D;
        }

        .building__form__wrap {
            position: relative;

            &::before {
                content: '';
                width: 70px;
                height: 70px;
                background: #B34215;
                display: block;
                position: absolute;
                top: -9px;
                left: -9px;
                z-index: 5;
            }
        }

        .building__form {
            position: relative;
            padding: 30px 33px;
            box-sizing: border-box;
            background: #FFFFFF;
            box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
            z-index: 5;


        }

        .building__form__header {
            display: block;
            font-style: normal;
            font-weight: 400;
            font-size: 15px;
            line-height: 120%;
            /* identical to box height, or 18px */
            text-align: center;
            color: #14162F;
            margin-bottom: 26px;

            b {
                display: block;
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 120%;

                /* or 24px */
                text-align: center;
                margin-bottom: 11px;

                a {
                    color: $main-color;
                    text-decoration: none;
                }
            }
        }

        .building__form__input {
            width: 100%;
            background: transparent;
            border: 1px solid #F1F1F1;
            outline: none;
            padding: 10px 15px;
            box-sizing: border-box;
            color: #fff $text-main-color;
            margin-bottom: 10px;

            &::placeholder {
                color: #C4C4C4;
            }
        }

        .building__form__submit {
            text-transform: unset;
            font-weight: 400;
            font-size: 15px;
            line-height: 140%;
            max-width: 100%;
            margin-bottom: 15px;
        }

        span {
            display: block;
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;
            text-align: center;

            color: #37383D;

            a {
                font: inherit;
                text-decoration: underline;
            }
        }
    }

    .developing {
        padding: 82px 0 0;
        background: #FAFDFF;

        .container {}

        .developing__header {
            max-width: 874px;
            margin: 0 auto 50px;
        }

        .developing__wrap {
            display: grid;
            grid-template-columns: minmax(310px, 432px) minmax(310px, 677px);
            column-gap: 31px;
        }

        .developing__desc {
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            line-height: 140%;

            /* or 25px */

            color: #37383D;
        }

        .developing__bg {
            width: 100%;
        }
    }

    .question {
        padding: 117px 0 148px;

        .container {}

        .question__header {
            margin-bottom: 116px;
        }

        .question__info-list {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 55px 61px;
        }

        .question__info-list__item {}

        .question__info-list__item__heading {
            font-style: normal;
            font-weight: 600;
            font-size: 40px;
            line-height: 140%;
            /* identical to box height, or 56px */
            color: #37383D;
            margin-bottom: 22px;
            display: block;
        }

        .question__info-list__item__desc {
            font-style: normal;
            font-weight: 300;
            font-size: 18px;
            line-height: 140%;
            /* or 25px */
            color: #37383D;
        }

        .question__info-list__item__list {
            list-style-type: disc;
        }

        .question__info-list__item__header {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 140%;
            /* identical to box height, or 34px */
            color: #37383D;

            display: flex;
            justify-content: start;
            align-items: center;

            margin-bottom: 20px;

            img {
                margin-right: 15px;
            }
        }

        .question__info-list__item__list__item {
            font-style: normal;
            font-weight: 300;
            font-size: 15px;
            line-height: 140%;

            /* or 21px */

            color: #37383D;
        }
    }
}

@media screen and (max-width: 1050px) {
    .container {
        box-sizing: border-box;
    }

    .headline .headline__img {
        display: none;
    }

    .catalog-page .building .building__wrapper {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }

    .catalog-page .building .building__form__wrap {
        &::before {
            display: none;
        }
    }

    .catalog-page .developing .developing__wrap {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }

    .catalog-page .question .question__info-list {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 740px) {
    .catalog-page .info-trigger {
        display: none;
    }

    .catalog-page .building .building__form__wrap {
        display: none;
    }

    .catalog-page .developing {
        display: none;
    }

    .catalog-page .question {
        display: none;
    }

    .catalog-page .headline .headline__desc {
        display: none;
    }

    .catalog-page .headline {
        padding: 50px 0 35px;
    }

    .catalog-page .catalog .breadcrumbs {
        display: none;
    }

    .catalog-page .catalog .catalog__sort__trigger {
        display: flex;
    }

    .catalog-page .catalog {
        background: #fff;
        padding-top: 0;
    }

    .catalog-page .catalog .catalog__body {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-direction: column-reverse;
    }

    .catalog-page .catalog .catalog__filters {
        position: absolute;
        top: 0;
        right: 0;
        max-width: 280px;
        width: 100%;
        height: 100vh;
        overflow-y: auto;
        overflow-x: hidden;

        transform: translateX(1000%);
        // visibility: hidden;
        display: none;
        transition: transform 0.5s;

        &._active {
            transform: translateX(0);
            // visibility: visible;
            display: block;
        }
    }

    .catalog-page .catalog .catalog__filters__mobile {
        display: block;
    }

    .catalog-page .catalog .catalog__filters__overlay {
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 100;
        background-color: rgba(0, 0, 0, 0.5);
        visibility: hidden;

        &._active {
            visibility: visible;
        }
    }

    .catalog-page .catalog .catalog__side-bar {
        margin-top: 100px;
    }

    .catalog-page .catalog {
        padding-bottom: 0;
    }

    .catalog-page .building {
        padding-top: 100px;
    }

    .catalog-page .catalog .catalog__sort__wrapper {
        flex-direction: column;
    }

    .catalog-page .catalog .catalog__sort__select-wrap {
        margin-left: 0;
    }

    .catalog-page .catalog .show-more {
        margin-top: -80px;
    }

    .catalog-page .catalog .catalog__banner {
        position: absolute;
        left: 50%;
        transform: translate(-50%, 0);
        // transform: translate(-50%, 40px);
        max-width: 335px;
        width: 100%;
    }
    .catalog-page .catalog .catalog__banner__btn {
        img {
            width: 150px;
        }
    }
    .catalog-page .catalog .catalog__banner__phone {
        font-size: 22px;
        line-height: 140%;  
    }
    .catalog-page .building {
        padding-top: 532px;
    }

    // .catalog-page .catalog .catalog__pager {
    //     display: flex;
    // }
}